import React from 'react';
import styled from '@emotion/styled';

import InputWithLabel, { IInputWithLabel } from './InputWithLabel';
import Button from './Button';

export interface IInputWithLabelButton extends IInputWithLabel {
  buttonLabel: string;
  buttonType: string;
  buttonClick?: any;
}

const InputWithLabelButton = ({
  buttonType,
  buttonLabel,
  buttonClick,
  ...props
}: IInputWithLabelButton) => {
  return (
    <Wrapper>
      <InputWithLabel {...props} />
      <StyledButton styleType={buttonType} onClick={buttonClick}>
        {buttonLabel}
      </StyledButton>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
`;

const StyledButton = styled(Button)`
  margin-left: 10px;
  padding: 15px 20px;
  white-space: nowrap;
  width: auto;
  min-width: 100px;
`;

export default InputWithLabelButton;
