import React, { useState, useEffect } from "react";
import styled from "@emotion/styled";
import { useHistory } from "react-router";

import Button from "../components/Button";
import InputWithLabel from "../components/InputWithLabel";
import RadioGroup from "../components/RadioGroup";
import InputWithLabelButton from "../components/InputWithLabelButton";
import Modal from "../components/Modal";
import AuthServiceWrapper from "../components/AuthServiceWrapper";

import { api } from "../utils/api";

const Register = () => {
  const history = useHistory();
  const [info, setInfo] = useState({
    email: "",
    password: "",
    rePassword: "",
    phone: "",
    birth: "",
    gender: "m",
  });
  const [error, setError] = useState({
    email: false,
    password: false,
    rePassword: false,
    phone: true,
    birth: false,
  });
  const [isRegistered, setIsRegistered] = useState(false);
  const [checkedEmail, setCheckedEmail] = useState(false);
  const [isOpenInfoModal, setIsOpenInfoModal] = useState(false);
  const [modalInfo, setModalInfo] = useState<{
    firstButtonLabel: string;
    onClickFirstButton: () => any;
    title: string;
    content: string;
  }>({
    firstButtonLabel: "",
    onClickFirstButton: () => null,
    title: "",
    content: "",
  });

  const [errorMessage, setErrorMessage] = useState("");
  const [isErrorModalOpen, setIsErrorModalOpen] = useState(false);
  const [emailErrorMessage, setEmailErrorMessage] = useState("중복된 이메일");

  const handleChange = (e: any) => {
    e.persist();
    setInfo((prevState) => ({ ...prevState, [e.target.name]: e.target.value }));
  };

  const handleCheckExistsEmail = async () => {
    // eslint-disable-next-line
    const emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    const validation = emailRegex.test(info.email.toLowerCase());
    if (!validation) {
      setEmailErrorMessage("올바르지 않은 이메일 형식");
      setError((prevState) => ({ ...prevState, email: true }));
      setModalInfo({
        firstButtonLabel: "확인",
        onClickFirstButton: () => setIsOpenInfoModal(false),
        title: "이메일 중복 확인",
        content: "올바르지 않은 이메일 형식입니다.",
      });
      setIsOpenInfoModal(true);
      return false;
    }
    const {
      data: { success, alert },
    } = await api.get("/email/check", {
      params: { email: info.email },
    });
    if (!success) {
      setError((prevState) => ({ ...prevState, email: true }));
      setEmailErrorMessage("중복된 이메일");
      setModalInfo({
        firstButtonLabel: "확인",
        onClickFirstButton: () => setIsOpenInfoModal(false),
        title: "이메일 중복 확인",
        content: alert,
      });
      setIsOpenInfoModal(true);
      return false;
    }
    setError((prevState) => ({ ...prevState, email: false }));
    setCheckedEmail(true);
    setModalInfo({
      firstButtonLabel: "확인",
      onClickFirstButton: () => setIsOpenInfoModal(false),
      title: "이메일 중복 확인",
      content: "사용 가능한 이메일 주소입니다.",
    });
    setIsOpenInfoModal(true);
    return true;
  };

  const handleSubmit = async () => {
    // Submit 시점에서 exist 테스트를 다시 진행함
    const res = await handleCheckExistsEmail();
    if (!res) {
      return;
    }
    const isPhoneValid = info.phone.startsWith("010");
    if (!isPhoneValid) {
      setModalInfo({
        firstButtonLabel: "확인",
        onClickFirstButton: () => setIsOpenInfoModal(false),
        title: "휴대폰 번호 확인",
        content: "올바른 연락처 형식이 아닙니다.",
      });
      setIsOpenInfoModal(true);
      return;
    }

    const isBirthValid = !!info.birth.match(/^\d{8}$/g);
    if (info.birth !== "" && !isBirthValid) {
      setModalInfo({
        firstButtonLabel: "확인",
        onClickFirstButton: () => setIsOpenInfoModal(false),
        title: "생년월일 확인",
        content: "올바른 생년월일 형식이 아닙니다.",
      });
      setIsOpenInfoModal(true);
      return;
    }

    try {
      const {
        data: { success, alert },
      } = await api.post("/join", {
        email: info.email,
        password: info.password,
        phone: info.phone,
        birth: info.birth ? info.birth : "",
        gender: info.gender ? info.gender : "",
      });
      if (success) {
        setIsRegistered(true);
      } else {
        if (alert) {
          setErrorMessage(alert);
          setIsErrorModalOpen(true);
        }
      }
    } catch (error) {
      if (error.response && error.response.alert) {
        setErrorMessage(error.response.alert);
        setIsErrorModalOpen(true);
      }
    }
  };

  useEffect(() => {
    if (info.rePassword && info.password !== info.rePassword) {
      setError((prevState) => ({ ...prevState, rePassword: true }));
    } else {
      setError((prevState) => ({ ...prevState, rePassword: false }));
    }
  }, [info.password, info.rePassword]);

  useEffect(() => {
    if (info.password.length < 8) {
      setError((prevState) => ({ ...prevState, password: true }));
    } else {
      setError((prevState) => ({ ...prevState, password: false }));
    }
  }, [info.password]);

  // useEffect(() => {
  //   if (info.phone.length > 0) {
  //     setError((prevState) => ({ ...prevState, phone: true }));
  //   } else {
  //     setError((prevState) => ({ ...prevState, phone: false }));
  //   }
  // }, [info.phone]);

  return (
    <>
      <AuthServiceWrapper
        title="회원가입"
        info={
          <>
            코드스코어는 이메일 아이디를 사용하고 있어
            <br />
            꼭, 실제 사용하고 계신 이메일 주소를 입력해주세요.
          </>
        }
      >
        <InputWrap>
          <InputWithLabelButton
            name="email"
            label="아이디(E-mail)"
            placeholder="아이디(E-mail)를 입력해주세요."
            buttonLabel="중복확인"
            buttonType="blue"
            buttonClick={handleCheckExistsEmail}
            onChange={handleChange}
            error={error.email}
            disabled={checkedEmail}
            errorMessage={emailErrorMessage}
          />
          <InputWithLabel
            name="password"
            label="비밀번호"
            placeholder="8자리 이상의 비밀번호를 입력해주세요."
            type="password"
            onChange={handleChange}
            error={error.password}
            errorMessage="8자리 이상으로 입력"
          />
          <InputWithLabel
            name="rePassword"
            label="비밀번호 확인"
            placeholder="8자리 이상의 비밀번호를 입력해주세요."
            type="password"
            onChange={handleChange}
            error={error.rePassword}
            errorMessage="비밀번호 불일치"
          />
          <InputWithLabel
            name="phone"
            label="휴대폰 번호"
            placeholder="휴대폰 번호를 입력해주세요."
            maxLength={11}
            onChange={handleChange}
            error={error.phone}
            errorMessage="신청하신 악보 작성이 완료된 경우 작성하신 번호로 알림이 갑니다."
          />
          <InputWithLabel
            name="birth"
            label="생년월일"
            placeholder="생년월일 8자리를 입력해주세요."
            maxLength={8}
            onChange={handleChange}
            error={error.birth}
          />
          <RadioGroup
            options={[
              { field: "남자", value: "m" },
              { field: "여자", value: "f" },
              { field: "선택안함", value: "" },
            ]}
            value={info.gender}
            onChange={(value: string) => setInfo({ ...info, gender: value })}
          />
        </InputWrap>
        <ButtonWrap>
          <Button styleType="blue" onClick={handleSubmit}>
            회원가입 완료
          </Button>
          <Button styleType="outline" onClick={() => history.goBack()}>
            뒤로
          </Button>
        </ButtonWrap>
      </AuthServiceWrapper>
      <Modal
        open={isRegistered}
        onClose={() => setIsRegistered(false)}
        firstButtonLabel="확인"
        onClickFirstButton={() => history.push("/auth/login")}
        title="회원가입 완료"
        content={
          <>
            회원가입이 완료되었습니다.
            <br />
            지금 바로 내가 원하는 악보를 찾아보세요.
          </>
        }
      />
      <Modal
        open={isOpenInfoModal}
        onClose={() => setIsOpenInfoModal(false)}
        {...modalInfo}
      />
      <Modal
        open={isErrorModalOpen}
        onClose={() => setIsErrorModalOpen(false)}
        firstButtonLabel="확인"
        onClickFirstButton={() => setIsErrorModalOpen(false)}
        title="에러"
        content={errorMessage}
      />
    </>
  );
};

const InputWrap = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;

  & > * {
    margin-bottom: 20px;
  }
`;

const ButtonWrap = styled.div`
  & > * {
    margin-bottom: 10px;
  }
`;

export default Register;
